      ],
      headers: [
        { text: "股票名称", value: "stockName", align: "center" },
        { text: "股票代码", value: "stockCode" },
        { text: "当前股价", value: "currentPrice" },
        // { text: "涨跌幅", value: "valueTime" },
        { text: "每股收益(元)", value: "earnPerShare" },
        { text: "每股现金流(元)", value: "cashFlowPerShare" },
        { text: "每股公积金(元)", value: "accumulationFundPerShare" },
        { text: "每股未分配利润(元)", value: "undistributedProfitPerShare" },
        { text: "流通股本(亿)", value: "circulationShareCapital" },
        { text: "流通市值(亿)", value: "circulationMarketCapital" },
      ],
      footers: {
        showCurrentPage: true,
        itemsPerPageText: "每页记录数",
        showFirstLastPage: true,
        itemsPerPageOptions: [8, 15, -1],
      },
      filters: [],
      dialog: false, //弹窗控制
      selected: "",
      title: "",
      describe: "",
      writeMode: false,
    };
  },
  created() {
    this.initData();
    this.setDescribe();
  },
  components: {},
  methods: {
    initData() {
      this.initFilters();
    },
    setDescribe() {
      getTitle().then((res) => {
        if (res.data.status === 200) {
          store.commit("setTitle", res.data.data[0]);
          this.describe = this.$store.state.describe;
        } else {
          notify("error", "标题和描述数据出错");
        }
      });
    },
    initStock() {
      getStocksByFilter(this.selected)
        .then((res) => {
          if(res.data.status===200){
            let headers = res.data.headers;
            let data = res.data.data;
            store.commit("setMetaData", JSON.parse(JSON.stringify(res.data.data)));
            headers = cleanMeta(headers);
            initHeaders2(headers,this);
            reMapData(data,headers);
            this.stocks = data;
            notify("success", "数据更新成功");
          }else {
            notify("error", "股票数据更新失败");
          }
        })
        .catch();
    },
    initFilters() {
      getFilters()
        .then((res) => {
          let filters = res.data.data;
          let list = [];
          for (let filtersKey in filters) {
            list.push(filters[filtersKey]);
          }
          this.filters = list;
          this.selected = list[list.length - 1];
          this.initStock();
          notify("success", "数据更新成功");
        })
        .catch();
    },
    changeSelect() {
      this.initStock();
    },
    changeItem(goto, item) {
      let stock = item.headers1;
      store.commit("setTitle2", "个股画像" + "——" + stock);
      store.commit("setItem", item);
      store.commit("setSelected", item._quarter);
      if (goto) {
        router.push(goto);
        this.$store.commit("setTitleWriteMode");
      }
    },
    changeWriteMode() {
      this.writeMode = !this.writeMode;
    },
    submit() {
      let data = {
        title: this.$store.state.title,
        describe: this.describe,
      };
      postTitle(data).then((res) => {
        if (res.data.status === 200) {
          this.setDescribe();
          this.changeWriteMode();
        } else {
          notify("error", "请求出错");
        }
      });
    },
  },
};
</script>
